.project-map {
  position: relative;

  #mapID {
    position: relative;
    z-index: 0;

    width: 100%;
    height: 75vh;

    @media (min-width: 768px) {
      height: 50vh;
    }
    @media (min-width: 1024px) {
      height: 75vh;
    }

    .leaflet-marker-pane {
      img[alt="mainMarker"] {
        z-index: 2000 !important;
        transform-origin: center;
      }
    }
  }

  .map__layer {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;

    background-image: linear-gradient(to bottom, white 0%, transparent 10% 90%, white 100%);

    
  }
}
